.mobile {
    overflow: hidden;

    top: 0;
    width: 100%;
    z-index: 1;

}

.mobi {
    display: flex;
    flex-direction: row;
    background-color: red;
    height: 3rem;

}

.mobile-menu {
    color: white;
    background-color: black;
    width: 200px;
    height: auto;
    padding-top: 10px;
}

.mobi svg {
    color: white;
    padding-right: 10px;
    margin-top: 12px;
    margin-left: 20px;
}

.mobi span {
    color: black;
    font-size: 26px;
    font-weight: 700;
    font-family: sans-serif;
    padding-top: 10px;
}

@media (min-width:600px) {
    .mobile {
        display: none;
    }

}