.tech {
    background-color: aquamarine;
    color: white;
    padding-left: 220px;
    padding-top: 20px;
}

.so-tech {
    display: flex;

}

.h1 {
    font-size: 48px;
    padding-left: 23rem;

    color: black;
    margin-top: 0;
    padding-top: 0;


}

.h4 {
    font-size: 18px;
    text-align: center;
    justify-content: center;
    margin-bottom: 2rem;
    color: yellowgreen;


}

.icons {
    display: flex;
    flex-direction: row;
    gap: 20px;
    align-items: center;
    justify-content: center;

}

.icon {
    display: flex;
    flex-direction: row;
    gap: 10px;
    width: 12rem;
    height: 3rem;
    border: 2px solid gray;
    border-radius: 6px;
    margin-bottom: 10px;

    margin-bottom: 2rem;
    -ms-flex-align: center;


}


.icon svg {
    cursor: pointer;
    padding-top: 7px;

}

.icon h3 {}


@media (max-width:600px) {
    .tech {
        padding: 0;
        margin: 0;
    }

    .h1 {
        padding: 0;
        margin: 0;
    }

    .icons {
        display: flex;
        flex-direction: column;

    }


}