.sidebar-section {
    display: flex;
    background-color: black;
}



.sidebar {
    width: 90px;
    height: 100vh;
    background-color: #1e1e2c;
    position: fixed;
}

.toogle {
    width: 200px;
}

.sidebar-toggle-icons {
    display: flex;
    justify-content: right;
    align-items: right;
    cursor: pointer;

}

.sidebar-toggle-icons p svg {
    background-color: #f29f67;
    color: white;
    margin-top: 30px;
}

@media (max-width: 600px) {
    .sidebar {
        display: none;
    }
}