.container {
    height: 300px;
    background-color: black;
    color: white;
    width: 100%;
}

.hii {
    margin-left: 200px;
    padding-left: 20px;
    padding-top: 70px;
    color: white;
}

.h2 {
    margin-left: 200px;
    padding-left: 20px;
    color: yellowgreen;
}

.content {}

.action {

    margin-left: 200px;
    padding-left: 20px;
    display: flex;
    gap: 10px;
}

.action button {
    width: 250px;
    margin-top: 40px;
    background-color: blue;
    border-radius: 10px;
    color: white;
    font-size: 24px;
}

.action button:hover {
    background-color: wheat;
    color: blue;
    transition: 1s;
    opacity: 80%;
}

/*8888888888888888888888*/

@media (max-width: 600px) {
    .container {

        padding: 0;
        margin: 0;
        margin-bottom: 20px
    }

    .hii {
        padding: 0;
        margin: 0;
        margin-bottom: 20px;
        margin-left: 10px;
        margin-top: 30px;
    }

    .h2 {
        padding: 0;
        margin: 0;
        margin-bottom: 50px;
        margin-left: 10px;
    }

    .action button {
        padding: 0;
        margin: 0;

    }

}