.img img {

    height: 150px;
    width: 150px;
    margin-top: 20px;
    border-radius: 90px;
    display: flex;
    vertical-align: middle;
    margin: 0 auto;
}

.item {
    margin-top: 20;
    color: white;
}

.link {
    display: flex;
    flex-direction: row;
    margin-top: 20px;
    cursor: pointer;
    align-items: baseline;

}

.link:hover,
.link:active {
    color: gray;
}

.link svg {
    margin-top: 0;
    margin-left: 10px;

}

.link span {
    font-size: 24px;
    padding-left: 10px;
}

.lee {
    font-size: 20px;
    font-weight: 600;
    font-family: sans-serif;
    margin-left: 20px;
    color: wheat;
    text-decoration: none;
    align-items: inline;
}

.lee:hover,
.lee:active {
    color: gray;
}