.footer {
    width: 100%;
    height: 2rem;
    margin-top: 10px;
    background-color: black;
    color: white;
}

.footer p {
    font-size: 24px;
    font-family: sans-serif;
    font-weight: 600;
    font-style: italic;
    text-align: center;
    justify-content: center;
    padding-bottom: 10px;
    padding-left: 300px;
}

/*8888888888888888888888*/
@media(max-width:600px) {
    .footer {
        padding: 0;
        margin: 0;
    }

    .footer p {
        padding: 0;
        margin: 0;
    }
}