.about {
    display: flex;
    margin-left: 200px;
    padding: 40px;
}

.rows {
    display: flex;
    flex-direction: row;
    gap: 40px;

}

.imp {}

.imp img {
    height: 300px;
    width: 400px;

}

.para {
    width: 500px;
}

.para h1 {
    text-align: center;
    margin-top: 40px;
    margin-bottom: 10px;
}

.para p {
    align-items: center;
    align-content: center;
    justify-content: center;
}



@media (max-width:600px) {
    .about {
        padding: 0;
        margin: 0;
    }

    .rows {
        display: flex;
        flex-direction: column;
        height: auto;
    }

    .imp img {
        padding-top: 10px;
        padding-left: 10px;
        padding-right: 10px;
        padding-bottom: 10px;
    }

    .para p {
        padding-top: 10px;
        padding-left: 10px;
        padding-right: 10px;
        padding-bottom: 10px;
    }
}