.contact {
    background-color: aquamarine;
    color: white;
    padding-left: 220px;
    padding-top: 20px;
    padding-bottom: 20px;
}

.cont {
    display: flex;
    flex-direction: row;
    gap: 30px;
}

.inn {
    display: flex;

}

.inn img {
    width: 500px;

}

.info {
    width: 500px;
    border: 1px solid black;
    border-radius: 10px;
    display: flex;
    flex-direction: column;
    background-color: white;
}

.info input {
    padding: 10px;
    margin-left: 10px;
    margin-top: 10px;
    margin-right: 10px;
    margin-bottom: 10px;
    border: 1px solid black;
}

.input {
    padding: 10px;
    margin-left: 10px;
    margin-top: 10px;
    margin-right: 10px;
    margin-bottom: 10px;
    border: 1px solid black;
}

.butt {
    width: 150px;
    margin-top: 10px;
    margin-bottom: 10px;
    margin-left: 10px;
    background-color: blue;
    border-radius: 10px;
    color: white;
    font-size: 20px;
}



@media (max-width:600px) {
    .contact {
        padding: 0;
        margin: 0;
    }

    .cont {
        display: flex;
        flex-direction: column;
    }

    .inn img {
        margin-top: 10px;
        margin-left: 10px;
        margin-right: 10px;
        margin-bottom: 20px;
    }

    .info {
        margin-top: 10px;
        width: 400px;
        margin-left: 10px;
        margin-right: 10px;
        margin-bottom: 20px;
        overflow: hidden;
    }
}